<template>
<section class="has-text-centered">
    <figure class="image is-64x64 container"><img class="rouned" :src="setting.avatar" src="../assets/logo.png" alt="logo" /></figure>
    <div class="content">
        <h5>{{setting.name}}</h5>
        <p>{{setting.desc}}</p>
    </div>
    <hr />
    <nav>
        <span class="tag"><a href="">nav</a></span>
        <span class="tag"><a href="">triathlon</a></span>
        <span class="tag"><a href="">nav</a></span>
        <span class="tag"><a href="">nav</a></span>
    </nav>
</section>
</template>

<script>
import {getSetting} from '../api/index'
export default {
  name: 'About',
  data(){
      return {
          setting:{}
      }
  },
  methods:{
      fetchDetail(){
            getSetting((res)=>{
                if(res.length>0){
                    this.setting=res[0];
                }
            })
        }
  },
mounted(){
    this.fetchDetail();
}
}
</script>


