<template>
<section>
  <Nav></Nav>  
  <div class="section">
    <div class="content" v-if="photos.length>0">
        <div class="tile is-ancestor">
            <div class="tile is-vertical is-3" v-for="i in 4">
                    <div class="tile is-child" v-for="item in photos">
                        <figure>
                            <a :href="item.src"><img :src="item.src" alt=""></a>
                            <figcaption>
                                <h6 class="subtitle">{{item.title}}</h6>
                            </figcaption>
                        </figure>
                    </div>
            </div>
        </div>
    </div>
    <div class="container section" v-else></div>
  </div>
  <Footer></Footer>
</section>
</template>
<style>
</style>

<script>
  import Nav from '../components/nav'
  import Footer from '../components/footer'
  import Hero from '../components/hero'  
  import {getPhotoList} from '../api/index'
  export default {
    data() {
      return {
          photos:[]
      }
    },
    watch: {
    },
    methods: {
        fetchList(){
            getPhotoList( res=>{
                this.photos= res;
            })
        }
    },
    components: {
      Nav,
      Footer
    },
    mounted(){
        this.fetchList();
    }
  };
</script>


