<template>
<section>
  <Nav></Nav>
  <section class="section">
    <div class="container" v-if="events.length>0">
        <div class="content" :key="item.objectId" v-for="item in events">
            <div class="item">
                <h3>
                    <router-link :to="{path: `/event/${item.objectId}`}">{{item.title}}</router-link>
                </h3>
            </div>
        </div>        
        <Table :data="data" :columns="columns"></Table>
    </div>  
    <div class="container content" v-else></div>
  </section>
</section>
 
</template>
<script>
  import Nav from '../../components/nav'
  import Footer from '../../components/footer'
  import {getEventList} from '../../api/index'
import { Table } from 'buefy/dist/components/table'
  export default {
    data() {
      return {
          events: [],
          data: [
                { 'id': 1, 'first_name': 'Jesse', 'last_name': 'Simmons', 'date': '2016-10-15 13:43:27', 'gender': 'Male' },
                { 'id': 2, 'first_name': 'John', 'last_name': 'Jacobs', 'date': '2016-12-15 06:00:53', 'gender': 'Male' },
                { 'id': 3, 'first_name': 'Tina', 'last_name': 'Gilbert', 'date': '2016-04-26 06:26:28', 'gender': 'Female' },
                { 'id': 4, 'first_name': 'Clarence', 'last_name': 'Flores', 'date': '2016-04-10 10:28:46', 'gender': 'Male' },
                { 'id': 5, 'first_name': 'Anne', 'last_name': 'Lee', 'date': '2016-12-06 14:38:38', 'gender': 'Female' }
            ],
            columns: [
                {
                    field: 'id',
                    label: 'ID',
                    width: '40',
                    numeric: true
                },
                {
                    field: 'first_name',
                    label: 'First Name',
                },
                {
                    field: 'last_name',
                    label: 'Last Name',
                },
                {
                    field: 'date',
                    label: 'Date',
                    centered: true
                },
                {
                    field: 'gender',
                    label: 'Gender',
                }
            ]
      }
    },
    watch: {
    },
    methods: {
        fetchList: function(){
            var that=this;
            getEventList(res=>{
                that.events=res;
            })
        }
    },
    components: {
      Nav,
      Footer,
      Table
    },
    mounted(){
        this.fetchList();
    }
  };
</script>



