<template>
    <form @submit.prevent="submit" class="section">
        <div class="field">
            <label class="label">Title</label>
            <div class="control">
                <input class="input" required="required" type="text" v-model="title" placeholder="Text input">
            </div>
        </div> 
        <div class="field">
            <label for="" class="label"></label>
            <div class="control">
                <file-upload
                class="btn btn-primary"
                post-action="/upload/post"
                extensions="gif,jpg,jpeg,png,webp"
                accept="image/png,image/gif,image/jpeg,image/webp"
                :multiple="true"
                :size="1024 * 1024 * 10"
                v-model="files"
                @input-filter="inputFilter"
                @input-file="inputFile"
                ref="upload">
                <i class="fa fa-plus"></i>
                Select files
                </file-upload>
                <button type="button" class="btn btn-success" v-if="!$refs.upload || !$refs.upload.active" @click.prevent="$refs.upload.active = true">
                <i class="fa fa-arrow-up" aria-hidden="true"></i>
                Start Upload
                </button>
                <button type="button" class="btn btn-danger"  v-else @click.prevent="$refs.upload.active = false">
                <i class="fa fa-stop" aria-hidden="true"></i>
                Stop Upload
                </button>
            </div>
        </div>
        <div class="field">
            <label class="label">Content</label>
            <div class="control">
                <mavon-editor v-model="content"/>
            </div>
        </div>
        <div class="field is-grouped">
            <div class="control">
                <button type="submit" class="button is-link"></button>
            </div>
        </div>
    </form>
</template>
<style>
.file-uploads {
  overflow: hidden;
  position: relative;
  text-align: center;
  display: inline-block;
}
.file-uploads.file-uploads-html4 input, .file-uploads.file-uploads-html5 label {
  /* background fix ie  click */
  background: #fff;
  opacity: 0;
  font-size: 20em;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.file-uploads.file-uploads-html5 input, .file-uploads.file-uploads-html4 label {
  /* background fix ie  click */
  background: rgba(255, 255, 255, 0);
  overflow: hidden;
  position: fixed;
  width: 1px;
  height: 1px;
  z-index: -1;
  opacity: 0;
}
</style>

<script>
import { mavonEditor } from 'mavon-editor'
import {newblog} from '../../../api/index'
import 'mavon-editor/dist/css/index.css'
import FileUpload from 'vue-upload-component'
  export default {
    data() {
      return {
          files: [],
          content: '',
          title: '',
          thumb: '',
          author: 'migao',
          id: ''
      }
    },
    watch: {
    },
    methods: {
        submit(){
            newblog(this.title,this.content,this.author,this.thumb, (res)=>{
                if(res){
                    this.$router.push({path:'/admin/blog'});
                }
            })
        },
        inputFilter(newFile, oldFile, prevent) {
            if (newFile && !oldFile) {
                // Before adding a file
                // 
                // Filter system files or hide files
                //  
                if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
                return prevent()
                }
                // Filter php html js file
                //  php html js 
                if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
                return prevent()
                }
            }
            },
            inputFile(newFile, oldFile) {
            if (newFile && !oldFile) {
                // add
                console.log('add', newFile)
            }
            if (newFile && oldFile) {
                // update
                console.log('update', newFile)
            }
            if (!newFile && oldFile) {
                // remove
                console.log('remove', oldFile)
            }
        }
    },
    components: {
      mavonEditor,
      FileUpload
    },
    mounted(){
    }
  };
</script>



