<template>
<section class="is-header">
    <nav class="navbar has-shadow" role="navigation" aria-label="main navigation">
    <div class="container">
        <div class="navbar-brand">
            <a class="navbar-item" href="/">
            TRI
            </a>

            <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            </a>
        </div>

        <div id="navbarBasicExample" class="navbar-menu">

            <div class="navbar-end">
            <router-link class="navbar-item is-tab" :key="nav.li" v-for="nav in navs" :to="nav.url" :class="{'is-active' : nav.isActive}">{{nav.li}}</router-link>
            </div>
        </div>
    </div>
    </nav>
</section>
</template>
<script>
export default {
  name: 'Nav',
  data () {
    return {
        navs:[{
            li: 'Home',
            url: '/',
            isActive:true
        },
        {
            li: 'Blog',
            url: '/blog'
        },
        {
            li: 'Events',
            url: '/event'
        },
        {
            li: 'Photos',
            url: '/gallery'
        },
        {
            li: 'About',
            url: '/about'
        }]
    }
  },
  
  methods: {
      getMenuList(){
          var page=this.$route.path;
          var ismatched=false;
          for(let i=0,len=this.navs.length;i<len;i++){                  
                this.navs[i].isActive=false;
                if(page==this.navs[i].url){
                    this.navs[i].isActive=true;
                    ismatched=true;
                }
          }
          if(ismatched){
              this.$forceUpdate();
          }
      }
  },
    mounted(){
        this.getMenuList();
    }
}
</script>



